* {
	margin: 0;
	padding: 0;
  }
  .navbar{
	background:#FFFFFF;
	padding: 0px;
	margin: 0px;
	max-height: 80px;
  }
  
  .nav_ul li {
	padding: 0 0.6rem;
  }
  
  .nav_ul a {
	font-size: 1.05rem;
	color: #000000 !important;
	font-weight: 400;
  }
  
  .btn1 {
	border: 0;
	padding: 0.5rem 1.8rem;
	color: #fff;
	background-color: #01193c;
  }
  
  .btn2 {
	border: 0;
	padding: 0.5rem 1.2rem;
	color: #fff;
	background-color: #f60838;
  }
  
  .Footer {
	padding: 2rem 0;
	margin-top: 15rem;
  }
  
  .ft-1 h3 {
	font-weight: 700;
	font-family: cursive;
	letter-spacing: 2px;
	padding: 0.5rem 0;
  }
  
  .ft-1 span {
	color: #f60838;
  }
  
  .ft-1 p {
	padding: 0rem 5rem 0.8rem 0;
	font-weight: 500;
  }
  
  .footer-icons i {
	padding: 0.4rem 0.5rem;
	background: #e1e1e1;
	color: #f60838;
	margin: 0 0.5rem;
	border-radius: 50%;
	
  }
  
  .footer-icons i:hover {
	background: #f60838;
	color: #fff;
	transition: 0.6s;
	cursor: pointer;
  }
  
  .Footer h5 {
	color: #f60838;
  }
  
  .ft-2 ul {
	list-style: none;
	padding-left: 0;
  }
  
  .ft-2 ul li {
	padding: 0.35rem 0;
	font-weight: 500;
  }
  
  .ft-2 ul a {
	text-decoration: none;
	color: #000;
	font-size: 1.06rem;
  }
  
  .ft-3 p {
	font-weight: 500;
	padding: 0.1rem 0;
	font-size: 1.06rem;
  }
  
  .ft-3 i {
	padding-right: 0.5rem;
  }
  
  .Last-footer {
	background: #f60838;
	text-align: center;
	padding: 1rem 0;
  }
  
  .Last-footer p {
	margin-bottom: 0;
	font-size: 1.2rem;
	font-weight: 700;
  }
/* background */
body{
    background:#e9eaec;
}
.background{
    background-color:rgb(255, 255, 255);
	margin: 10px;
    box-shadow: 7px 7px 10px rgb(212, 209, 209);
}